import React, { useEffect, useState } from "react";
import { Link } from "@reach/router";
import Header2 from "../menu/header2";
import Join from "../components/Join";
import Copyright from "../components/Copyright";
import FlipCountdown from "@rumess/react-flip-countdown";
import CounterInput from "react-counter-input";
import Web3 from "web3";
import ReactLoading from "react-loading";
import NINJA from "../../contracts/NC.json";

const Mint = () => {
  const [metamaskConnected, setMetamaskConnected] = useState(false);
  const [mintLoading, setMintLoading] = useState(false);
  const [netalert, setNetalert] = useState(false);
  const [alertmsg, setAlertmsg] = useState("");
  const [account, setAccount] = useState();
  const [networkId, setNetworkId] = useState();
  const [NINJAcontract, setNINJAcontract] = useState(null);
  const [web3, setWeb3] = useState();
  const [mintamount, setmintamount] = useState(1);
  const [totalToken, settotalToken] = useState(0);

  useEffect(() => {
    async function listenMMAccount() {
      if (window.ethereum) {
        connectToMetamask();
        window.ethereum.on("accountsChanged", async function() {
          connectToMetamask();
        });
        window.ethereum.on("chainChanged", async function() {
          connectToMetamask();
        });
      } else {
        setNetalert(true);
        setAlertmsg("None Ethereum Browser! Install Metamask");
      }
      if (NINJAcontract) {
        const totalToken = await NINJAcontract.methods.totalToken().call();
        settotalToken(totalToken);
      }
    }
    listenMMAccount();
  }, []);
  const connectToMetamask = async () => {
    settotalToken(0);
    if (window.ethereum) {
      setNetalert(false);
      window.web3 = new Web3(window.ethereum);
      window.ethereum.enable();
      const web3 = window.web3;
      setWeb3(web3);
      const accounts = await web3.eth.getAccounts();
      if (accounts.length == 0) {
        setMetamaskConnected(false);
      } else {
        const accounts = await web3.eth.getAccounts();
        // console.log("connect info:", accounts);
        setAccount(accounts[0]);
        // console.log(accounts[0]);
        setMetamaskConnected(true);
        const networkId = await web3.eth.net.getId();
        setNetworkId(networkId);
        // console.log("netid", networkId);
        const networkData = NINJA.networks[networkId];
        // console.log("netdata", networkData);
        if (networkData) {
          const NINJAcontract = new web3.eth.Contract(
            NINJA.abi,
            networkData.address
          );
          setNINJAcontract(NINJAcontract);
          const totalToken = await NINJAcontract.methods.totalToken().call();
          settotalToken(totalToken);
        } else {
          setNetalert(true);
          setAlertmsg("Please switch to main network");
        }
      }
    } else if (window.web3) {
      setNetalert(false);
      window.web3 = new Web3(window.web3.currentProvider);
      const web3 = window.web3;
      setWeb3(web3);
      const accounts = await web3.eth.getAccounts();
      if (accounts.length == 0) {
        setMetamaskConnected(false);
      } else {
        const accounts = await web3.eth.getAccounts();
        // console.log("connect info:", accounts);
        setAccount(accounts[0]);
        // console.log(accounts[0]);
        setMetamaskConnected(true);
        const networkId = await web3.eth.net.getId();
        setNetworkId(networkId);
        // console.log("netid", networkId);
        const networkData = NINJA.networks[networkId];
        // console.log("netdata", networkData);
        if (networkData) {
          const NINJAcontract = new web3.eth.Contract(
            NINJA.abi,
            networkData.address
          );
          setNINJAcontract(NINJAcontract);
          const totalToken = await NINJAcontract.methods.totalToken().call();
          settotalToken(totalToken);
        } else {
          setNetalert(true);
          setAlertmsg("Please switch to main network");
        }
      }
    } else {
      setNetalert(true);
      setAlertmsg("None Ethereum Browser! Install Metamask");
    }
  };

  const mintNft = async () => {
    setMintLoading(true);
    let price = "0.06";
    let tokenAmount = mintamount;
    // console.log("mintamount", mintamount);
    if (account !== "") {
      let e;
      try {
        var correctPrice = web3.utils.toBN(
          web3.utils.toWei(price, "ether").toString()
        );
      } catch (u) {
        console.log("err", u);
      }
      try {
        e = await NINJAcontract.methods.mint(tokenAmount).estimateGas({
          value: correctPrice * tokenAmount,
          from: account,
        });
      } catch (u) {
        console.log("Insufficient Fund");
      }
      let d = await web3.eth.getGasPrice();
      let c;

      try {
        c = await NINJAcontract.methods.mint(tokenAmount).send({
          from: account,
          gas: parseInt(e),
          gasPrice: parseInt(1.2 * d),
          value: correctPrice * tokenAmount,
          maxFeePerGas: null,
        });
        setMintLoading(false);
      } catch (u) {
        console.log("Mint Error", u);
        setMintLoading(false);
      }

      if (c) {
        return c.status;
      }
    } else {
      setMintLoading(false);
    }
    const totalToken = await NINJAcontract.methods.totalToken().call();
    settotalToken(totalToken);
  };
  return (
    <div>
      <section className="section_2">
        <div className="minttable row col-lg-12 col-md-12 col-sm-12 d-flex container align-items-center justify-content-between">
          <div
            className="d-flex item flex-column col-lg-6 col-md-6 align-items-center"
            style={{ color: "white", textAlign: "center" }}
          >
            <h1
              className="mintitle"
              style={{ color: "white", fontSize: "50px" }}
            >
              <b>
                MINT YOUR <span style={{ color: "red" }}>NC</span>
              </b>
            </h1>
            <p style={{ fontSize: "20px" }}>
              5,555 Unique Ninja Champions NFTs
            </p>
            <div className="mint-body text-left">
              {totalToken === 0 ? (
                ""
              ) : (
                <div className="d-flex justify-content-between">
                  <h1
                    className="tokencount mb-2"
                    style={{ color: "white", margin: "auto" }}
                  >
                    {totalToken} / 5,555 Sold
                  </h1>
                </div>
              )}
              <div className="d-flex justify-content-between">
                <div className="flex-1">Price</div>
                <div className="mint-value">{(0.06).toFixed(2)} ETH</div>
              </div>
              <div className="d-flex justify-content-between">
                <div className="flex-1">Amount</div>
                <CounterInput
                  className="amount-input"
                  min={1}
                  max={20}
                  count={mintamount}
                  onCountChange={(count) => setmintamount(count)}
                />
                <div className="flex-1 text-right">
                  <button
                    className="btn-max"
                    onClick={() => {
                      setmintamount(20);
                    }}
                  >
                    max
                  </button>
                </div>
              </div>
              <div className="d-flex justify-content-between">
                <div className="flex-1">Total</div>
                <div className="mint-value">
                  {(0.06 * mintamount).toFixed(2)} ETH
                </div>
              </div>
              <div className="d-flex justify-content-between">
                <div className="flex-1" style={{color:"#ae31d1"}}>Next Drop: 6 hours</div>
                <div className="mint-value" style={{color:"#ae31d1"}}>
                  {(0.05 * mintamount).toFixed(2)} ETH
                </div>
              </div>
              <div className="d-flex align-items-center justify-center">
                {mintLoading ? (
                  <div className="loading">
                    <ReactLoading type="spin" color="#0affff" />
                  </div>
                ) : (
                  <>
                    {networkId === 1 ? (
                      <>
                        <button className="btn btn-mint" onClick={mintNft}>
                          MINT
                        </button>
                      </>
                    ) : (
                      <>
                        <button className="btn btn-mint disable">
                          Unavailable
                        </button>
                        {/* <p>Make sure your Metamask wallet is connected.</p> */}
                      </>
                    )}
                  </>
                )}
              </div>
            </div>
          </div>
          <div className="d-flex item flex-column lg-6 col-md-6 align-items-center mintvideo">
            {/* <img src="../../../assets/nc.gif" style={{width:"90%"}}></img> */}
            <video autoPlay muted loop className="videoroadmapmint">
              <source src="./assets/G.mp4" type="video/mp4" />
            </video>
          </div>
        </div>
      </section>
      <section className="d-flex flex-column no-bottom">
        <Header2
          connectToMetamask={connectToMetamask}
          metamaskConnected={metamaskConnected}
          account={account}
          networkId={networkId}
          netalert={netalert}
          alertmsg={alertmsg}
        />
        <Join />
        <Copyright />
      </section>
    </div>
  );
};
export default Mint;
