import React, { useEffect, useState } from "react";
import Carousel from "react-spring-3d-carousel";
import uuidv4 from "uuid";
import { config } from "react-spring";

const Unique = () => {
  const [state, setState] = useState({
    goToSlide: 0,
    offsetRadius: 2,
    showNavigation: false,
    config: config.slow,
  });

  const slides = [
    {
      key: 1,
      content: <img src="./assets/hfm.png" className="CrouselImg" alt="1" />,
    },
    {
      key: 2,
      content: <img src="./assets/cmo.png" className="CrouselImg" alt="2" />,
    },
    {
      key: 3,
      content: <img src="./assets/articart.png" className="CrouselImg" alt="3" />,
    },
    {
      key: 4,
      content: <img src="./assets/helmet.png" className="CrouselImg" alt="4" />,
    },
    {
      key: 5,
      content: <img src="./assets/hat.png" className="CrouselImg" alt="5" />,
    },
    {
      key: 6,
      content: <img src="./assets/director.png" className="CrouselImg" alt="6" />,
    },
  ].map((slide, index) => {
    return { ...slide, onClick: () => setState({ goToSlide: index }) };
  });
  const leftchangeslide = () => {
    var index;
    if (state.goToSlide === 0) index = 6;
    else index = state.goToSlide - 1;
    setState({ goToSlide: index });
  };
  const rightchangeslide = () => {
    var index;
    if (state.goToSlide === 6) index = 0;
    else index = state.goToSlide + 1;
    setState({ goToSlide: index });
  };
  const onChangeInput = (e) => {
    setState({
      [e.target.name]: parseInt(e.target.value, 10) || 0,
    });
  };

  return (
    <>
      <div
        id="unique"
        className="item col-lg-12 col-md-12 col-sm-12 d-flex mb-5 container flex-column align-items-center justify-content-center"
        style={{ color: "#aaa", marginTop: "50px" }} data-aos="fade-up"
        data-aos-duration="1500"
        data-aos-easing="ease-in-quad"
      >
        {/* <div className="container"> */}
        <h1 className="uniquetitle">What makes Ninja Champions unique?</h1>
        <p style={{ textAlign: "left" }}>
          <strong>Ninja Champions </strong> are ready for NFT and metaverse.
           All Ninjas have unique traits and leveled ranking.<br/>
          1. Ranking of <strong>Ninja Champions</strong> is leveled as kage,
          jounin, special jounin, chuunin, genin and academy. From a start, all holders have academy role.<br/>2. <strong>Ninja Champions</strong> are categorized by an ability of characters. <br/>Strength
          contains attacking, defending power. Intelligence contains
          sightseeing, leading ability. Agility contains speed and moving
          distance value. 
        </p>
        {/* </div> */}
        <div className="carousel"> 
          <Carousel
            className="CrouselPart"
            slides={slides}
            goToSlide={state.goToSlide}
            offsetRadius={state.offsetRadius}
            showNavigation={state.showNavigation}
            animationConfig={state.config}
          /> 
          <div className="experience-left-arrow" onClick={leftchangeslide}>
            <div className="slider-left"></div>
          </div>
          <div className="experience-right-arrow" onClick={rightchangeslide}>
            <div className="slider-right"></div>
          </div>
        </div>
      </div>
    </>
  );
};
export default Unique;
