import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { FaBars } from "react-icons/fa";
import "./header.scss";

const Header = () => {
  const [openMenu, setOpenMenu] = React.useState(false);
  const closeMenu = () => {
    setOpenMenu(false);
  };
  useEffect(() => {
    const header = document.getElementById("myHeader");
    // const totop = document.getElementById("scroll-to-top");
    const sticky = header.offsetTop;
    const scrollCallBack = window.addEventListener("scroll", () => {
      if (window.pageYOffset > sticky) {
        header.classList.add("sticky");
        // totop.classList.add("show");
      } else {
        header.classList.remove("sticky");
        // totop.classList.remove("show");
      }
      if (window.pageYOffset > sticky) {
        closeMenu();
      }
    });
    return () => {
      window.removeEventListener("scroll", scrollCallBack);
    };
  }, []);

  const toggleMenu = () => {
    const menuItem = document.getElementById("ToggleMenu");
    menuItem.classList.toggle("toggleMenu");
  };

  return (
    <>
      <div>
        <header id="myHeader" className="navbar white ">
          <div className="mainside ">
            <div className="menuItemmint">
              <span className="menulink ToggleBtn" onClick={() => toggleMenu()}>
                <FaBars />
              </span>
              <div className="menuItem">
                <a className="menulink" href="#outline">
                  <b>STORY</b>
                </a>
                <a className="menulink" href="#unique">
                  <b>UNIQUE</b>
                </a>
                <a className="menulink" href="#roadmap">
                  <b>ROADMAP</b>
                </a>
                <a className="menulink" href="#team">
                  <b>TEAM</b>
                </a>
                <a className="menulink" href="#faq">
                  <b>FAQ</b>
                </a>
              </div>
            </div>
            <div className="menuBtnArea">
              <div className="btn-icons">
                <div>
                  <a
                    href="https://twitter.com/champions_ninja"
                    target="_blank"
                    style={{ display: "flex" }}
                  >
                    <img src="../../../assets/IconTwiter.png" alt="" />
                  </a>
                </div>
                <div>
                  <a
                    href="https://discord.gg/ninjachampions"
                    target="_blank"
                    style={{ display: "flex" }}
                  >
                    <img src="../../../assets/IconDiscord.png" alt="" />
                  </a>
                </div>
                <div>
                  <a
                    href="https://www.instagram.com/ninjachampionsnft"
                    target="_blank"
                  >
                    <img src="../../../assets/IconInstagram.png" alt="" />
                  </a>
                </div>
                <div>
            <a href="https://opensea.io/collection/ninjachampions" target="_blank"><img src="../../../assets/IconOpensea.png" alt="" /></a>
            </div>
              </div>
              <div className="space"></div>
              <div className="btn-main">
                <Link to="/mint">
                  <span style={{ color: "white" }}>PreSale Mint</span>&nbsp;&nbsp;
                  <img src="../../../assets/Tool_Star.png" alt="" />
                </Link>
              </div>
            </div>
          </div>

          <div className="TogglePart" id="ToggleMenu">
            <div className="ToggleMenuItem" onClick={() => toggleMenu()}>
              <a className="toggleLink" href="#outline">
                <b>STORY</b>
              </a>
              <a className="toggleLink" href="#unique">
                <b>UNIQUE</b>
              </a>
              <a className="toggleLink" href="#roadmap">
                <b>ROADMAP</b>
              </a>
              <a className="toggleLink" href="#team">
                <b>TEAM</b>
              </a>
              <a className="toggleLink" href="#faq">
                <b>FAQ</b>
              </a>
              <div className="toggle-btn-main">
                <Link to="/mint">
                  <span style={{ color: "white" }}>PreSale Mint</span>&nbsp;&nbsp;
                </Link>
              </div>
            </div>
          </div>
        </header>
      </div>
    </>
  );
};
export default Header;
