import React, { useEffect } from "react";
import styled from "styled-components";
import { bounce } from "react-animations";
const Copyright = () => {
  return (
    <>
      <div
        id="joinus"
        className="col-lg-12 col-md-12 col-sm-12 d-flex mb-5 flex-column align-items-center justify-content-center text-center"
        style={{ color: "white", padding:"30px" }}
      >
        <h2 style={{ color: "#F8D407" }}>
        <img
              alt="logo"
              src="./assets/logo.png"
              style={{ width: "60px", height:"55px", marginBottom:"5px" }}
            />
        </h2>
        <h6>
          <b>© 2022 Copyrights - Ninja Champions</b>
        </h6>
      </div>
    </>
  );
};
export default Copyright;
