import React, { useEffect } from "react";
import styled from "styled-components";
import { bounce } from "react-animations";
const Join = () => {
  return (
    <>
      <div
        id="joinus"
        className="minjoin col-lg-12 col-md-12 col-sm-12 d-flex mb-5 flex-column align-items-center justify-content-center text-center"
        style={{
          color: "white",
          background: "#070414",
          padding: "50px 50px",
          marginTop: "40px",
        }}
      >

        <h2 style={{ color: "#F8D407" }}>
          <strong>NINJA CHAMPIONS</strong>
        </h2>
        <h1 className="mintitle" style={{ color: "white", fontSize:"50px" }}>
          <b>JOIN US</b>
        </h1>
        <div className="col-lg-12 col-md-12 col-sm-12 d-flex row">
          <div className="col-lg-3 col-md-2 col-sm-2"></div>
          <div className="socialicons col-lg-6 col-md-8 col-sm-8 d-flex justify-content-between">
            <a href="https://twitter.com/champions_ninja" rel="noreferrer" target="_blank">
              <img
                className="footerimg"
                alt="twitter"
                src="./assets/twitter.png"
              />
            </a>
            <a href="https://discord.gg/ninjachampions" rel="noreferrer" target="_blank">
              <img
                className="footerimg"
                alt="discord"
                src="./assets/discord.png"
              />
            </a>
            <a href="https://www.instagram.com/ninjachampionsnft" rel="noreferrer" target="_blank">
            <img
              className="footerimg"
              alt="instagram"
              src="./assets/instagram.png"
            /></a>
          </div>
          <div className="col-lg-3 col-md-2 col-sm-2"></div>
        </div>
      </div>
    </>
  );
};
export default Join;
