import React, { useEffect } from "react";
import { Link } from "react-router-dom";

const Header2 = (props) => {
  const { metamaskConnected,  account, connectToMetamask,networkId,netalert, alertmsg } = props;
  const [openMenu, setOpenMenu] = React.useState(false);
  const closeMenu = () => {
    setOpenMenu(false);
  };
  useEffect(() => {
    const header = document.getElementById("myHeader");
    // const totop = document.getElementById("scroll-to-top");
    const sticky = header.offsetTop;
    const scrollCallBack = window.addEventListener("scroll", () => {
      if (window.pageYOffset > sticky) {
        header.classList.add("sticky");
        // totop.classList.add("show");
      } else {
        header.classList.remove("sticky");
        // totop.classList.remove("show");
      }
      if (window.pageYOffset > sticky) {
        closeMenu();
      }
    });
    return () => {
      window.removeEventListener("scroll", scrollCallBack);
    };
  }, []);
  return (
    <header id="myHeader" className="navbar2 white">
      {/* {metamaskConnected?(networkId && Number(networkId)===1?"": */}
      <div className="col-lg-12 col-md-12 col-sm-12 d-flex text-center justify-content-between" style={{backgroundColor:"red"}}>
        {netalert?<h1 style={{color:"white",padding:"5px",margin:"auto"}}>{alertmsg}</h1>:""}
      </div>
      <div className="mainside mt-2">
        <div className="menuItemmint">
          <a className='menulink' href="/"><img src="../../../assets/mintlogo.png" width="70"></img><span className="NinjaText" style={{fontSize:"25px",marginTop:"10px"}}>{' '} Ninja Champions</span></a>
        </div>
        <div className="menuBtnArea">
          <div className="btn-icons">
            <div>
              <a href="https://twitter.com/champions_ninja" target="_blank" style={{display:"flex"}}><img src="../../../assets/IconTwiter.png" alt="" /></a>
            </div>
            <div>
            <a href="https://discord.gg/ninjachampions" target="_blank" style={{display:"flex"}}><img src="../../../assets/IconDiscord.png" alt="" /></a>
            </div>
            <div>
            <a href="https://www.instagram.com/ninjachampionsnft" target="_blank"><img src="../../../assets/IconInstagram.png" alt="" /></a>
            </div>
            <div>
            <a href="https://opensea.io/collection/ninjachampions" target="_blank"><img src="../../../assets/IconOpensea.png" alt="" /></a>
            </div>
          </div>
          <div className="space"></div>          
          <div className="btn-main">
          {metamaskConnected === true ? (
            <span>
              {account &&
                `${account.substring(0, 4)}...${account.substring(
                  account.length - 4
                )}`}
            </span>
          ) : (
            <a onClick={connectToMetamask}>Connect Wallet</a>
          )}&nbsp;&nbsp;
              <img src="../../../assets/Tool_Star.png" alt="" />
          </div>
        </div>
      </div>
    </header>
  );
};
export default Header2;
