import React, { Component } from "react";
import "./style.css";

class CountDown extends Component {
  constructor(props) {
    super(props);
    this.count = this.count.bind(this);
    this.state = {
      days: 0,
      minutes: 0,
      hours: 0,
      secounds: 0,
      time_up: ""
    };
    this.x = null;
    this.deadline = null;
  }
  count() {
    var now = new Date().getTime();
    var now_temp = new Date();
    console.log("asdfasdf", now_temp);
    var t = this.deadline - now;
     console.log("timer---- ", t);
    var dd = Math.floor(t / (1000 * 60 * 60 * 24));
    var hh = Math.floor((t % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    var mm = Math.floor((t % (1000 * 60 * 60)) / (1000 * 60));
    var ss = Math.floor((t % (1000 * 60)) / 1000);

    var days = dd < 10 ? "0" + dd : dd;
    var hours = hh < 10 ? "0" + hh : hh;
    // var hours = 9;
    var minutes = mm < 10 ? "0" + mm : mm;
    var seconds = ss < 10 ? "0" + ss : ss;

    this.setState({ days, minutes, hours, seconds });
    if (t < 0) {
      // this.props.onhandleSetTimerStatus(true);
      clearInterval(this.x);
      this.setState({
        days: 0,
        minutes: 0,
        hours: 0,
        seconds: 0,
        time_up: "TIME IS UP"
      });
    }
  }
  componentDidMount() {
    const WhitelistsaleDate = new Date(Date.UTC(2022, 3, 8, 6, 0, 0));
    const PresaleDate = new Date(Date.UTC(2022, 3, 9, 7, 0, 0));
    const PublicSale = new Date(Date.UTC(2022, 3, 19, 14, 0, 0));
    this.deadline = PublicSale.getTime();
    console.log("deadline", PublicSale);
    this.x = setInterval(this.count, 1000);
  }
  render() {
    const { days, seconds, hours, minutes } = this.state;
    return (
      <div id="countdown" className="">
        <div className="date-item">
          <div className="box">
            <p id="day">{days}</p>
            <span className="text">Days</span>
          </div>
        </div>
        <div className="date-item">
          <div className="box">
            <p id="hour">{hours}</p>
            <span className="text">Hours</span>
          </div>
        </div>
        <div className="date-item">
          <div className="box">
            <p id="minute">{minutes}</p>
            <span className="text">Minutes</span>
          </div>
        </div>
        <div className="date-item">
          <div className="box">
            <p id="second">{seconds}</p>
            <span className="text">Seconds</span>
          </div>
        </div>
      </div>
    );
  }
}

export default CountDown;
